.sidebar-nav-wrap {
    text-align: center;
}

.item-wrap {
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    padding: 8px 0 10px 0;
}

.sidebar-nav-wrap .item-wrap:nth-of-type(1) {
    border-top: 1px solid rgba(255, 255, 255, 0.3);
}

span.plus {
    display: inline-flex;
    justify-self: center;
    float: right;
    align-items: center;
    justify-content: center;
    align-self: center;
}

.item-wrap a {
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
}

.item-wrap a {
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
    color: #fff;
    text-decoration: none;
    display: block;
    padding: 0 8px;
    text-align: left;
}

.item-wrap a svg {
    padding-right: 16px;
}

p.recent-files {
    font-weight: 400;
    font-size: 10px;
    text-transform: uppercase;
    color: #ffffff;
    opacity: .5;
    text-align: left;
    padding-left: 9px;
    margin-top: 9px;
}